//尝试发送安装日志
function getLocalNum() {
    try {
        let extensionCrm = window.localStorage.getItem('extensionCrm');
        if (extensionCrm) {
            chrome.storage.local.set({extensionCrm: true});
        }
        let data;
        if (window.localStorage.getItem('last-wid')) {
            data = window.localStorage.getItem('last-wid')
            myNumber = data.split('@')[0].substring(1);
        } else {
            data = window.localStorage.getItem('last-wid-md')
            myNumber = data.split(':')[0].substring(1);
        }
    } catch (e) {
        // console.log("获取本地手机号出错");
    }
    chrome.storage.local.set({userPhoneNum: myNumber, loadTimes: 1});
    // 给background传递用户手机号，用来给GA日志传递信息
    chrome.runtime.sendMessage({userPhoneNum: myNumber});

}

// 获取本地手机号
window.onload = function () {
    getLocalNum()
    const browserData = {
        browserInfo: getBrowser(),
        platform: window.navigator.platform,
        language: window.navigator.language,
        phoneNum: myNumber,
    };
    chrome.storage.local.set({...browserData});
    // 发送安装日志
    chrome.storage.local.get(['installLogFlag'], function (res) {
        if (res.installLogFlag === false) {
            sendLog(300011, browserData)
            chrome.storage.local.set({installLogFlag: true})
            // 如果是第一次安装并且是获取不到手机号的扫码页面，需要跟background通信让其设置卸载页面
            if (!myNumber) {
                chrome.runtime.sendMessage({installLogFlag: true});
            }
        }
    })
};

