                                let allDataVideo = retryFailedSendData.allDataVideo;
                                let allDataDocument = retryFailedSendData.allDataDocument;
                                // continue缓存数据
                                let continueSendData = getContinueSendData(phoneNumList, phoneNumSuccessList, phoneNumFailList,
                                    repeatList, oldSuccessAndFailList, 0, content, mediaType, minNum, maxNum, excelData,
                                    sendMessageStatisticsType, allDataImg, allDataVideo, allDataDocument);
